@import url(https://fonts.googleapis.com/css?family=Quicksand:400);
@import url(https://fonts.googleapis.com/css?family=Raleway:300);

*{
    box-sizing: border-box;
}

body{
    background: #22262a;
    margin: 0;
}
.heroes-section{
    display: grid;
    grid-gap: 1rem;
    align-items: center;
    padding: 2rem;
    grid-template-columns: repeat(4,1fr);
    grid-template-rows: auto;
    justify-items: center;

    .hero-card-wrapper{
        color: red;
        background-color: black;
        border-radius: 1rem;
        padding: 0.5rem;
        font-weight: bold;
    }
}
header{
    margin: 0 auto;
    display: flex;
    justify-content: center
}


.nav-link {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    color: #fff;
    height: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border-bottom: 2px solid transparent;
}
.nav-link {
    font-family: DIN Next W01 Regular,Trebuchet MS,Helvetica,Arial,sans-serif;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 1px;
    line-height: 16px;
    text-transform: uppercase;
}
.nav-upper {
    height: 52px;
    border-bottom: 1px solid #393939;
    padding: 0 20px;
}

.nav-linkContainer {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    padding: 5px;
    &:hover{
            outline: -webkit-focus-ring-color auto 5px;
            outline-color: #e8b63e;
            outline-style: auto;
            outline-width: 5px;
    };
}
.nav-linkWrapper {
    &:hover{
        border-bottom: 2px solid #eb2328;
    };
    text-transform: uppercase;
    font-weight: 800;
    padding: 0 0.5rem;
    cursor: pointer;
    list-style: none;
}

.nav-logo {
    width: 214px;
    margin: auto;
    left: 0;
    right: 0;
    height: 52px;
}

.nav-tabAndLogoContainer,.nav-lower {
    max-width: 1200px;
    margin: auto;
}


.navigation-container a {
    text-decoration: none!important;
}

.nav-lower, .nav-tabAndLogoContainer, .nav-upper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.navigation__container a {
    text-decoration: none!important;
}
.nav-link {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    color: #fff;
    height: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border-bottom: 2px solid transparent;
}

.nav-link {
    font-family: DIN Next W01 Regular,Trebuchet MS,Helvetica,Arial,sans-serif;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 1px;
    line-height: 16px;
    text-transform: uppercase;
}

@media (min-width: 839px){
    .main-footer {
        padding: 80px 0 30px;
    }
}
.main-footer {
    background: #000;
    width: 100%;
    padding: 50px 0 38px;
    position: absolute;
    bottom: 0;
}

@media (min-width: 839px){
    .main-footer__container {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        padding: 0 1.25rem;
    }
}

@media (min-width: 1241px){
    .main-footer__container {
        padding: 0;
    }
}

@media (min-width: 961px){
    .main-footer__container {
        padding: 0 20px;
    }
}

.main-footer__container {
    max-width: 1200px;
    padding: 0 30px;
    margin: 0 auto;
}

.slider__item .mvl-card {
    white-space: normal;
    z-index: 30;
}
.mvl-card--explore, .mvl-card--large, .mvl-card--lob, .mvl-card--media, .mvl-card--merch {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    position: relative;
}
.mvl-card--explore {
    color: #fff;
    max-width: 100%;
    -webkit-transition: color .3s;
    transition: color .3s;
}

.mvl-card--explore .card-thumb-frame {
    background: #eb2328;
    margin: 0;
    overflow: hidden;
    padding: 0;
}

@media (min-width: 601px)
{
    .mvl-card--explore .card-thumb-frame .img__wrapper {
        height: 210px;
    }
}

.mvl-card--explore .card-thumb-frame .img__wrapper {
    position: relative;
    overflow: hidden;
    height: 170px;
}
figure.img__wrapper {
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.mvl-card--explore .card-thumb-frame figure img {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    -webkit-transition: all .2s linear;
    transition: all .2s linear;
    overflow: hidden;
    -o-object-position: top center;
    object-position: top center;
}
.mvl-card img, .mvl-card picture {
    display: block;
    margin: 0;
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: center center;
    object-position: center center;
    padding: 0;
    width: 100%;
    height: 100%;
}

.mvl-card--explore .card-body.is-sliding {
    background: #151515;
    color: #37474f;
    -webkit-transition: color .3s;
    transition: color .3s;
}
.mvl-card .card-body {
    overflow: hidden;
    position: relative;
}
@media (min-width: 768px){
    .mvl-card--explore .card-body {
        padding: 16px 24px 17px 20px;
    }
}

.mvl-card--explore .card-body {
    -moz-osx-font-smoothing: grayscale;
    background: none;
    border: none;
    color: inherit;
    height: 145px;
    padding: 16px 24px 17px 16px;
    position: relative;
    vertical-align: middle;
    z-index: 30;
}

.hero-card-wrapper{
    width: 66.2299%;
}

.mvl-card--explore .card-body .card-footer {
    bottom: 17px;
    position: absolute;
    padding-right: 10px;
    text-align: left;
}
@media (min-width: 768px)
{
    .mvl-card--explore .card-footer {
        left: 20px;
    }
}

.mvl-card--explore .card-body .card-footer__secondary-text {
    color: #c3c3c3;
}
.mvl-card--explore .card-body .card-footer__micro-description, .mvl-card--explore .card-body .card-footer__secondary-text {
    font-family: DINNextW01-CondensedMed,Trebuchet MS,Helvetica,Arial,sans-serif;
    font-size: 11px;
    letter-spacing: 1px;
    line-height: 12px;
    text-transform: uppercase;
    -webkit-transform: translate3d(0) scale(1);
    transform: translate3d(0) scale(1);
    -webkit-transition: all .17s ease-in-out;
    transition: all .17s ease-in-out;
}

.mvl-card--explore .card-body .card-footer__micro-description {
    color: #999;
    text-transform: uppercase;
    font-size: 16px;
    line-height: 16px;
}